.navbar > .navbar-form {

    /* Clean layout for form outside of navbar-collapse */
    @media (max-width: @grid-float-breakpoint) {
        display: inline-block;
        margin: 0;
        border: 0;
    }
}

.navbar-collapse {

    /* Remove space for navbar in default view */
    @media (min-width: @grid-float-breakpoint) {
        padding-left: 0;
    }
}

.navbar-worldskills {
    .navbar-inverse;

    margin-bottom: 30px;
    min-height: @navbar-worldskills-height;

    background-image: url(data:image/png;base64,);
    background-size: 1000px 100px;
    background-repeat: no-repeat;
    background-position: top center;

    .navbar-brand {
        font-size: 24px;
        font-family: @brand-font-family;
        font-weight: 500;
        padding: @navbar-worldskills-padding-vertical (2 * @navbar-padding-horizontal) @navbar-worldskills-padding-vertical 105px;
        background-image: url(data:image/png;base64,);
        background-repeat: no-repeat;
        background-size: 65px 45px;
        background-position: 15px 26px;
    }

    .navbar-nav {
        margin: (@navbar-worldskills-padding-vertical / 2) -@navbar-padding-horizontal;

        @media (min-width: @grid-float-breakpoint) {
            > li {
                margin: 0 5px;

                > a {
                    border-radius: 25px;
                    padding-top: 10px;
                    padding-bottom: 10px;
                    margin-top: 10px;
                }
                
                &.open > a {
                    border-radius: 15px 15px 0 0;
                }
            }
        }
    }

    .navbar-form {
        margin-top: ((@navbar-worldskills-height - 36px) / 2);
        margin-bottom: ((@navbar-worldskills-height - 36px) / 2);
    }

    .navbar-toggle {
        margin-top: ((@navbar-worldskills-height - 36px) / 2);
    }    
}
